import React, { Component } from 'react';
import { connect } from 'react-redux';

import { setPageData } from '../../actions/page';
import InputField from '../customFields/inputField'
import { getValue } from '../../utils/formatter';
import { validate } from '../../utils/validation';
class ShareLink extends Component {
    constructor(props) {
        super(props);
        this.state = this.intialiseState();
    }


    intialiseState = (data = {}) => {
        return {
            fb: getValue(data, 'fb', ''),
            tw: getValue(data, 'tw', ''),
            insta: getValue(data, 'insta', ''),
            linkedIn: getValue(data, 'linkedIn', ''),
            index: getValue(data, 'index', 0),
            type: getValue(data, 'type', ''),
            errorMessages: {},
            isValid: {}
        }
    }

    componentDidMount() {
        if (this.props.data) {
            this.setState({ ...this.intialiseState(this.props.data) });
        }
    }

    validate = (name, value) => {
        let { isValid, errorMessages } = this.state;
        let error = validate(name, value, { type: 'text', regexType: name == 'linkedIn' ? 'url' : name });
        isValid[name] = error.isValid;
        error.isValid
            ? delete errorMessages[name]
            : (errorMessages[name] = error.errorMsg);
        this.setState({ isValid, errorMessages });
    };

    inputChange = (e, id) => {
        let {
            target: { name, value, checked, type }
        } = e;

        this.setState({ [name]: type == 'checkbox' ? checked : value }, () => {
            this.validate(name, value);
        });
    };

    onSubmit = () => {
        if (Object.keys(this.state.errorMessages).length == 0) {
            let payload = this.state;
            delete payload.errorMessages;
            delete payload.isValid;
            this.props.setPageData({ ...payload, page: 'anioisocial' });
            this.props.closeModal();
        }
    }


    render() {
        let { fb, tw, insta, linkedIn, errorMessages, isValid } = this.state;
        return (
            <div className='eventModal share-link'>
                <div className='mb15 pr'><span><i className="fa fa-facebook-square"></i></span>
                    <InputField name="fb" groupClass="input-style" value={fb} isValid={isValid.fb} errorMsg={errorMessages.fb}
                        inputChange={this.inputChange} dom={{ placeholder: '' }} />
                </div>
                <div className='mb15 pr'><span><i className="fa fa-twitter"></i></span>
                    <InputField name="tw" groupClass="input-style" value={tw} isValid={isValid.tw} errorMsg={errorMessages.tw}
                        inputChange={this.inputChange} dom={{ placeholder: '' }} />
                </div>
                <div className='mb15 pr'><span><i className="fa fa-instagram"></i></span>
                    <InputField name="insta" groupClass="input-style" value={insta} isValid={isValid.insta} errorMsg={errorMessages.insta}
                        inputChange={this.inputChange} dom={{ placeholder: '' }} />
                </div>
                <div className='mb15 pr'><span><i className="fa fa-link"></i></span>
                    <InputField name="linkedIn" groupClass="input-style" value={linkedIn} isValid={isValid.linkedIn} errorMsg={errorMessages.linkedIn}
                        inputChange={this.inputChange} dom={{ placeholder: '' }} />
                </div>
                <div className='text-right'>
                    <div className='btn' onClick={this.onSubmit}>add</div>
                </div>
            </div>
        );
    }
}
export default connect(null, { setPageData })(ShareLink);
